<template>
  <div style="padding: 20px 20px 20px 20px;width: 100%">
    <div  style="margin-right: 3%;margin-left:3%;padding-top: 20px;margin-top:3%;width:90%;height:80%;background-color: white;box-shadow: 0 0 10px #C0C0C0;margin-bottom: 5%">
      <div style="margin-left: 6%;margin-top: 4%;">

        <div style="margin: 3%;display: flex;font-size: 15px">
<!--   左边三个-->
          <div style="width: 50%;font-size: 20px">
            <div>
              任务编号：{{data.taskId}}
            </div>
            <div>
              任务名称：{{data.taskName}}
            </div>
            <div>
              开始时间：{{data.timeBegin}}
            </div>
          </div>
<!-- 右边三个         -->
          <div style="width: 50%;">
            <div>
              结束时间：{{data.timeEnd}}
            </div>
            <div>
              执行人员：{{data.userName}}
            </div>

          </div>


        </div>

<!--    表格部分-->
        <el-table
            :data="data.meter"
            style="width: 100%">
          <el-table-column
              prop="valueName"
              label="仪表名称"
              width="180">
          </el-table-column>
          <el-table-column
              prop="checkResult"
              label="数值"
              width="180">
            <template slot-scope="scope">
              <span :style="{ color: scope.row.result === 'true' ? 'green' : 'red' }">{{ scope.row.checkResult }}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="department"
              label="所属部门">
          </el-table-column>
          <el-table-column
              prop="owningDevice"
              label="所属设备">
          </el-table-column>
        </el-table>
        <div style="width: 60% ;border-left:grey solid 1px;height:90%;">
          <div style="font-weight: bold;font-size: 24px;margin:20px 0 0 30px ">具体概述</div>
          <div style="margin:20px 0 0 30px;height: 100%; font-size: 20px;width:100%;" v-html="data.data"></div>
        </div>

      </div>

  </div>
  </div>
</template>
<script>
export default {
  name: "TaskCheckMeter",
  data() {
    return {
      data:{},
    };
  },
  created() {
    this.load(this.$route.query.taskId)
  },
  methods: {
    async load(taskId){
      try {
        const recordTaskResponse = await this.$request.get('/recordTask/get', {
          params: { taskId: taskId }
        });
        const numericValueResponse = await this.$request.get('/numericValue/get', {
          params: { taskId: taskId }
        });

        this.data = recordTaskResponse.data;
        this.data.meter = numericValueResponse.data;
        console.log(this.data);
      } catch (error) {
        console.error(error);
      }
    }
  }
}
</script>
<style scoped>
</style>